@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
.task {
  padding: 5px 20px;
  border-radius: 5px;
  font-size: 14px;
  margin: 10px 0px;
  background-color: rgb(236, 219, 255);
}

.Card {
    box-shadow: 1px 1px 1px 0px rgba(0,0,0,0.5);
    border-radius: 10px;
    background-color: white;
}

.Card__title {
    background-color: var(--main-color);
    color: white;
    letter-spacing: 1px;
    margin: 0;
    padding: 10px;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    text-align: center;
}

.completed {
    background-color: #00992b;
}

.Card__content {
    padding: 30px;
}

.Card__tasks {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 300px));
    grid-column-gap: 10px;
    -webkit-column-gap: 10px;
            column-gap: 10px;
}
.PlanningGrid {
  padding: 30px 10px;
  display: grid;
  grid-template-columns: 1fr;
  grid-row-gap: 10px;
  row-gap: 10px;
}

header {
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--main-color-darker);
  font-size: 20px;
  font-weight: bold;
  color: white;
  letter-spacing: 1px;
}

.container {
  max-width: 1000px;
  margin: 0 auto;
}

:root {
  --main-color: #0079BF;
  --main-color-darker: rgb(0, 86, 136, 1);
  --main-color-transparent: rgba(0, 121, 191, 0.1)
}

body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', 'Helvetica', sans-serif, system-ui;
  background-color: rgba(0, 121, 191, 0.1);
  background-color: var(--main-color-transparent);
  height: 100vh;
}

.members {
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 10px;
}

.members span {
  margin: 15px 0;
}

.taskStatus {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
}

.completedStatus,
.uncompletedStatus {
  width: 40px;
  height: 20px;
  margin-right: 10px;
  display: inline-block;
}

.completedStatus {
  background-color: #00992b;
}

.uncompletedStatus {
  background-color: #0079BF;
  background-color: var(--main-color)
}
